@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: Poppins;
}

html {
  background: url(/intro-component-with-signup-form-master/static/media/bg-intro-desktop.059183ee.png) no-repeat center center fixed;
  background-color: hsl(0, 100%, 74%);
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  color: #fff;
}

a:link, a:visited, a:active {
  color: hsl(0, 100%, 74%);
  font-weight: 700;
  text-decoration: none;
}

a:hover {
  color: hsl(249, 10%, 26%);
  font-weight: 800;
}

input:focus, textarea:focus, select:focus {
  outline: none;
}
.Left {
    text-align: left;
    height: 400px;
    width: 500px;
    margin: 150px 50px 10px 40px;
}

#learn {
    font-size: 1.25cm;
    text-align: left;
}

.leftcontent {
    text-align: left;
    font-size: 16px;
}
.login {
    background-color: #FFF;
    border-radius: 8px;
    box-shadow: 0 8px 1px rgba(0, 0, 0, 0.158);
    padding: 25px;
    text-align: center;
}


input, select, textarea {
    margin: 10px;
    border: hsl(246, 39%, 85%);
    border: 2px solid #f0f0f0;
    border-radius: 6px;
    width: 90%;
    padding: 15px;
    font-weight: 600;
}

input:hover, input:focus, textarea:focus, select:focus {
    border: 2px solid hsl(246, 25%, 77%);
}

.error {
    background-image: url(/intro-component-with-signup-form-master/static/media/icon-error.b72f792e.svg);
    background-repeat: no-repeat;
    background-position: right;
    background-position-x: 98%;
    background-color: #fff;
    border: 2px solid hsl(0, 100%, 74%);
    color: hsl(0, 100%, 74%);
}

input.error::-webkit-input-placeholder {
    color: #fff;
}
input.error:hover, input.error:focus {
    border: 2px solid  hsl(0, 47%, 49%);
}

button {
    background-color: hsl(154, 59%, 51%);
    color: white;
    width: 97%;
    padding: 15px;
    margin: 10px;
    border: none;
    border-radius: 6px;
    box-shadow: inset 0 -5px 1px hsl(154, 59%, 39%);
    font-weight: 500;
}

button:hover {
    font-weight: 800;
}

button:active {
    background-color:hsl(154, 72%, 57%);
}

#oops {
    color: hsl(0, 100%, 74%);
    font-size: 10px;
    font-style: italic;
    text-align: right;
    margin: -5px 5px 0px -5px;
}

.disclaimer {
    font-size: 9px;
    color: #afafaf;
  }

.right {
    margin: 100px 50px 50px 50px;
    -webkit-align-self: center;
            align-self: center;
    width: 500px;
  }
  
  .tryit {
    text-align: center;
    background-color: hsl(248, 32%, 49%);
    border-radius: 8px;
    box-shadow: 0 8px 1px rgba(0, 0, 0, 0.158);
    padding: 2px;
  }
